espond-to(lg) {\n      font-weight: 500;\n      font-size: 16px;\n      line-height: 19px;\n    }\n  }\n}\n\n.all-cost {\n  margin-top: 24px;\n  span.all {\n    font-weight: normal;\n    color: $color-gray;\n    font-size: 14px;\n    line-height: 17px;\n\n    @include respond-to(md) {\n      font-size: 18px;\n      line-height: 21px;\n    }\n\n  }\n\n\n}\nspan.cost {\n  font-weight: 500;\n  color: $color-dark;\n  font-size: 18px;\n  line-height: 21px;\n\n  @include respond-to(md) {\n    font-weight: 500;\n    font-size: 32px;\n    line-height: 38px;\n  }\n}\n","$color-orange: #F4B12F;\r\n$color-dark: #1A1819;\r\n$color-gray: #767676;\r\n$color-light-gray: #F1F1F1;","@import 'colors';\r\n\r\n$container-max-widths: (\r\n        sm: 100%,\r\n        md: 740px,\r\n        lg: 1080px,\r\n        xl: 1280px,\r\n);\r\n$grid-breakpoints: (\r\n        xs: 0,\r\n        sm: 576px,\r\n        md: 767px,\r\n        lg: 1200px,\r\n        xl: 1365px,\r\n);\r\n\r\n$font-family-base: 'Rubik', sans-serif;\r\n$breadcrumb-item-padding-x: 0.1rem;\r\n$breadcrumb-active-color: $color-orange;\r\n$breadcrumb-divider-color: $color-gray;\r\n\r\n/// Mixin to manage responsive breakpoints\r\n/// @author Fulliton\r\n/// @param {String} $breakpoint - Breakpoint name\r\n/// @require $breakpoints\r\n@mixin respond-to($breakpoint) {\r\n  // If the key exists in the map\r\n  @if map-has-key($grid-breakpoints, $breakpoint) {\r\n    // Prints a media query based on the value\r\n    @media (min-width: map-get($grid-breakpoints, $breakpoint)) {\r\n      @content;\r\n    }\r\n  }\r\n\r\n    // If the key doesn't exist in the map\r\n  @else {\r\n    @warn \"Unfortunately, no value could be retrieved from `#{$breakpoint}`. \"\r\n        + \"Available breakpoints are: #{map-keys($grid-breakpoints)}.\";\r\n  }\r\n}\r\n\r\n@mixin respond-up($breakpoint) {\r\n  // If the key exists in the map\r\n  @if map-has-key($grid-breakpoints, $breakpoint) {\r\n    // Prints a media query based on the value\r\n    @media (max-width: map-get($grid-breakpoints, $breakpoint)) {\r\n      @content;\r\n    }\r\n  }\r\n\r\n    // If the key doesn't exist in the map\r\n  @else {\r\n    @warn \"Unfortunately, no value could be retrieved from `#{$breakpoint}`. \"\r\n        + \"Available breakpoints are: #{map-keys($grid-breakpoints)}.\";\r\n  }\r\n}"],"sourceRoot":""}]);
